.banner {
    width: 100%;
    position: relative;
    img {
        width: 100%;
        height: auto;
    }
    .btn-box {
        position: absolute;
        width: 235px;
        height: 120px;
        bottom: 8%;
        right: 20%;
        a {
            display: block;
            img {
                width: 234px;
                height: 50px;
                margin-bottom: 25px;
            }
        }
    }
}

// <!-- 介绍1 -->
.suggest-top {
    padding-top: 94px;
    padding-bottom: 50px;
    h2 {
        text-align: center;
        font-size: 44px;
        color: #ee583f;
        font-weight: normal;
        margin-bottom: 50px;
    }
    p {
        text-align: center;
        font-size: 24px;
        line-height: 40px;
    }
    .info {
        width: 100%;
        margin-top: 150px;
        img {
            display: block;
            float: left;
        }
        .text {
            float: left;
            width: 560px;
            h3 {
                color: #ee583f;
                font-size: 34px;
                font-weight: normal;
                margin-top: 140px;
                margin-bottom: 50px;
            }
            p {
                text-align: left;
                font-size: 24px;
                line-height: 40px;
            }
        }
    }
}

//  <!-- 中部展示 -->
.show-pic {
    width: 100%;
    position: relative;
    img {
        width: 100%;
        height: auto;
    }
    p {
        position: absolute;
        font-size: 44px;
        width: 630px;
        color: #fff;
        text-align: center;
        top: 35%;
        left: 30%;
    }
}

//   <!-- 介绍2 -->
.suggest-bottom-box {
    width: 100%;
    background-color: #efefef;
    .suggest-bottom {
        padding-top: 94px;
        .info {
            width: 100%;
            img {
                display: block;
                float: left;
            }
            .text {
                float: left;
                width: 380px;
                margin-left: 20px;
                h3 {
                    color: #ee583f;
                    font-size: 34px;
                    font-weight: normal;
                    margin-top: 140px;
                    margin-bottom: 50px;
                }
                p {
                    text-align: left;
                    font-size: 24px;
                    line-height: 40px;
                }
            }
        }
    }
}

// 下载
.download {
    height: 280px;
    padding-top: 130px;
    div {
        float: left;
        width: 600px;
        p {
            text-align: center;
            font-size: 44px;
            line-height: 100px;
            span {
                color: #ee583f;
                font-size: 60px;
            }
        }
        a {
            display: block;
            width: 302px;
            height: 62px;
            margin-bottom: 32px;
            margin-top: 15px;
            border-radius: 35px;
            overflow: hidden;
            margin-left: 175px;
            transition: all .3s;
        }
        .apple {
            background: url(../images/index/组66.png) -2px -2px no-repeat;
        }
        .android {
            background: url(../images/index/组65.png) -2px -2px no-repeat;
        }
        .apple:hover {
            background: url(../images/index/组63.png) -2px -2px no-repeat;
            background-color: #000;
        }
        .android:hover {
            background: url(../images/index/组64.png) -2px -2px no-repeat;
            background-color: #000;
        }
    }
}